body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}a{text-decoration:none}input{border:none;outline:none}body *{font-family:'Roboto', sans-serif}h1,h3{margin:0}button{border:none;outline:none}.App{max-width:1150px;margin:0 auto;overflow:hidden}ul{padding:0;margin:0;list-style-type:none}@media (max-width: 1200px){.navi{position:fixed;top:150px;display:flex;flex-direction:column;right:calc(50% - 75px);text-align:center;z-index:50}.navi__element{margin-bottom:16px;color:white}.overlay{position:fixed;padding:0px;margin:0px;top:0px;left:0px;width:100%;height:100%;background:rgba(0,0,0,0.8);z-index:20}.calculatorContact{z-index:50;position:fixed;top:350px;display:flex;flex-direction:column;right:calc(50% - 108px);text-align:center}.calculatorContact li{margin-bottom:32px}.culture{grid-template-columns:50% 50%}.estimation-main,.solutions{width:920px;padding:20px 20px 12px}.from__language,.to__language{width:380px}}

.banner{display:flex;margin-top:63px;padding:0 16px}.banner-texts{display:flex;flex-direction:column;align-items:flex-start}.banner-texts__header{font-size:62px;line-height:1.125;font-weight:600;margin-bottom:.35em}.banner-texts__subheader{font-size:20px;line-height:1.6;margin-bottom:24px;font-weight:400}.banner-texts__calculator{box-shadow:0 0 0 1px #0055b8;border-radius:4px;background-color:#0055b8;color:white;padding:15px 16px 17px;font-size:18px;font-weight:500;margin:8px 16px 8px 0;line-height:24px}.banner-texts__about{box-shadow:0 0 0 1px #ccd4de;border-radius:4px;font-size:18px;font-weight:400;margin:8px 16px 8px 0;padding:15px 16px 17px;min-width:176px;text-align:center}.offer__img{width:96px;margin-bottom:24px}.offers__header{font-size:40px;line-height:1.3;font-weight:600;flex-basis:100%;flex-shrink:0;text-align:center;margin-bottom:50px}.offer__header{font-size:24px;line-height:1.33;text-align:center;font-weight:500;min-height:62px}.offer__link{font-size:16px;line-height:20px;color:#0055b8;text-align:center;font-weight:500;display:block}.offer__content{font-size:16px;line-height:24px;font-weight:400;margin-bottom:10px;text-align:center;min-height:144px}.offers{margin-top:111px;display:flex;flex-wrap:wrap;justify-content:space-between;padding:0 16px;margin-bottom:80px}.offer{width:33%;display:flex;flex-direction:column;align-items:center;max-width:320px}.testimonial{display:flex;flex-direction:column;align-items:center;padding-left:16px}.testimonial__img{border-radius:50%;width:96px}.testimonial__content{font-size:24px;line-height:40px;display:block;text-align:center;margin:15px 0 20px}.testimonial__company{font-size:16px;color:#768190}.testimonials__list{display:flex;width:100px;justify-content:space-between;margin-top:14px}.testimonials__list__button{width:16px;border-radius:50%;height:16px;cursor:pointer;background-color:#ccd4de}.testimonials__list__button--active{width:16px;border-radius:50%;height:16px;cursor:pointer;background-color:#0055b8}.overall__