ding:30px 50px 12px;background-color:#F2F5F7;display:flex;flex-wrap:wrap;justify-content:space-between}.characters__textarea{width:280px;outline:none;display:flex;justify-content:space-between;font-size:16px;line-height:24px;border-radius:2px;border-width:0;transition:box-shadow .15s ease-in-out;box-shadow:0 0 0 1px #abb3bd,0 1px 2px rgba(0,0,0,0.16);white-space:nowrap;padding:11px 16px 13px;overflow:hidden;text-overflow:ellipsis;background-color:white;width:100%;height:24px}.estimation__button{margin:24px auto;padding:11px 16px 13px;border:none;font:inherit;color:inherit;background:none;background-color:#0055b8;border-radius:2px;color:#fff;font-size:16px;font-weight:500;line-height:24px;box-shadow:0 0 0 1px #0055b8;transition:background-color .25s ease-in-out,box-shadow .25s ease-in-out;text-align:center;text-decoration:none;display:inline-block;white-space:nowrap;-webkit-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer}.solutions{width:1120px;background-color:#F2F5F7;padding:30px 50px 12px;display:flex;flex-direction:column;justify-content:center;align-items:center}.solutions__mail-input{border-radius:4px;box-shadow:2px 2px 8px 0 rgba(0,0,0,0.16);padding:4px;border:none}.solutions__mail-input--invalid{border-radius:4px;box-shadow:2px 2px 8px 0 rgba(0,0,0,0.16);padding:4px;border:1px solid red}.solutions__list{padding:8px 50px 12px;display:flex;width:100%}.solution{display:flex;flex-direction:column;align-items:center;padding:48px 16px 24px;background-color:#fff;width:calc(100% / 3);margin-top:24px;border-radius:2px;box-shadow:0 0 0 1px #abb3bd;overflow:hidden}.solution__icons{display:flex;flex-direction:column;justify-content:center;align-items:center}.solution__icons__icon{display:flex;align-items:center;justify-content:center;line-height:20px;margin-left:4px;font-size:16px;font-weight:400;margin-bottom:8px}.solution__order{pointer-events:none;margin:0;padding:11px 16px 13px;border:none;font:inherit;color:inherit;background:none;background-color:gray;border-radius:2px;color:#fff;font-size:16px;font-weight:500;line-height:24px;box-shadow:0 0 0 1px gray;transition:background-color .25s ease-in-out,box-shadow .25s ease-in-out;text-align:center;text-decoration:none;display:inline-block;white-space:nowrap;-webkit-user-select:none;-ms-user-select:none;user-select:none}.solution__order--active{margin:0;cursor:pointer;pointer-events:auto;padding:11px 16px 13px;border:none;font:inherit;color:inherit;background:none;background-color:#0055b8;border-radius:2px;color:#fff;font-size:16px;font-weight:500;line-height:24px;box-shadow:0 0 0 1px #0055b8;transition:background-color .25s ease-in-out,box-shadow .25s ease-in-out;text-align:center;text-decoration:none;display:inline-block;white-space:nowrap;-webkit-user-select:none;-ms-user-select:none;user-select:none}.solution__header{line-height:24px;margin-bottom:16px;font-size:22px}.solutions__mail-description{margin-bottom:8px}.solution__price{font-size:24px;line-height:1.3;font-weight:500;margin:40px 0;cursor:pointer}

.logo{width:72px}.navi{display:flex}.navi__element{margin:0 16px;font-size:16px;line-height:24px}.menu{display:flex;justify-content:space-between;padding:18px 0;margin-top:22px;align-items:center;padding:0 16px}.calculatorContact{display:flex}.calculatorContact__contact{box-shadow:0 0 0 1px #ccd4de;border-radius:4px;font-size:16px;font-weight:400;padding:7px 16px 9px;background-color:white}.calculatorContact__contact a{margin-bottom:34px}.calculatorContact__calculator{box-shadow:0 0 0 1px #00479a;border-radius:4px;background-color:#00479a;font-size:16px;font-weight:400;color:white;padding:7px 16px 9px;margin-left:7px;text-decoration:none}

.footer-left__img{width:100x;height:100px;margin-right:16px;align-self:center}.footer-left__text{max-width:367px;padding-right:12px;font-size:14px;line-height:24px;font-weight:400;margin-top:24px}.footer-right__text{max-width:367px;font-size:14px;line-height:24px;font-weight:400;margin-top:24px}.footer-left{display:flex;flex-direction:column}.footer{display:flex;justify-content:space-between;padding:80px 0 40px 0}.footer-right__link-container{display:flex;align-items:center;margin-bottom:37px}.footer-right__link{margin-left:16px;color:#0055b8;font-size:16px;line-height:20px;color:#0055b8;transition:color .25s ease-in-out;font-weight:500}.footer-right__icon{fill:#abb3bd !important}.mail-message{display:none}

